.project {
    width: 300px;
    background-color: lightgrey;
    border: 1px solid;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    flex: 1 1 auto;
}
.imgContainer {
    height: 200px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.imgContainer img {
    height: 100%;
    width: 100%;
    transition: 1s;
}

.button {
    height: 100%;
    width: 100%;
    transition: 1s;
}

.button:hover {
    transform: scale(1.2);

}